
import {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import { getVideogames } from "../actions";
import { Link } from "react-router-dom";
import Card from "./Card";

export default function Home() {
    const dispatch = useDispatch();
    const allVideogames = useSelector(state => state.videogames);

    useEffect(() => {
        dispatch(getVideogames());
},[dispatch])


function handleClick(ev){
ev.prefentDefault()
dispatch(getVideogames())
}
    return (
        <div className="home-page">
            <Link to="/videogame">Crear videojuego</Link>
            <h1>Miguel Game Over</h1>
            <button onClick={ev =>{handleClick(ev)}}>
                Volver a cargar todos los Videojuegos
            </button>
            <div>
                <select>
                    <option value="asc">Ascendente</option>
                    <option value="desc">Descendente</option>
                </select>
                <select>
                    <option value="Todos">Todos</option>
                    <option value="Creado">Creados</option>
                    <option value="Existente">Existentes</option>
                </select>
              {allVideogames?.map((c) => {
                  return(
                       
                  <Link to={"/home/" + c.id}>
                        <Card key={c.id} image={c.image} name={c.name} genres={c.genres} />
                  </Link>
                  
    )
                  })}
    
            </div>
        </div>
    )
}
